<template>
  <div class="d-flex flex-column my-10" style="width: 100vw">
    <v-card class="bscCard">
      <div class="d-flex justify-space-between mt-2">
        <v-btn
          dark
          color="orange darken-1"
          class="mt-2 mr-4"
          :loading="loading"
          :disabled="loading"
          @click="metamaskAddRusCoin()"
        >
          {{$t('bsc.addRusCoin')}}
          <template v-slot:loader>
            ...
          </template>
        </v-btn>
      </div>
    </v-card>

<!--    <v-dialog persistent v-model="showCheck" maxWidth="520" class="calculator" overlay-opacity="0.7">
    </v-dialog>-->
  </div>
</template>

<script>

export default {
  name: 'Bsc',
  components: { },
  data: () => ({
    loading: false,
  }),
  computed: {
  },
  methods: {
    openWindow (url) {
      window.open(url, 'transaction');
    },
    async metamaskAddRusCoin () {
      if (!window.ethereum?.isMetaMask) {
        alert(this.$t('bsc.noMetaMask'))
      }
      try {
        const wasAdded = await window.ethereum.request({
          method: 'wallet_watchAsset',
          params: {
            type: 'ERC20', // Initially only supports ERC20, but eventually more!
            options: {
              address: '0xb5F8eC2e6190Bc79e4a6D64168Bd0BA406c31073', // The address that the token is at.
              symbol: 'RSC', // A ticker symbol or shorthand, up to 5 chars.
              decimals: 5, // The number of decimals in the token
              image: 'https://ruscoin.vip/img/icons/mstile-150x150.png', // A string url of the token logo
            },
          },
        });
        console.log(wasAdded)
      } catch (error) {
        console.log(error)
      }
    },
  },
  async mounted () {
  }
}
</script>

